@charset "UTF-8";

@media all {

    input.invalid,
    textarea.invalid,
    select.invalid,
    div.invalid {
        border-color: var(--danger);

        &:focus {
            border-color: var(--danger);
        }
    }

    .select.invalid {
        border-color: var(--danger);

        &:after {
            border-top-color: var(--danger);
        }
    }

    label.invalid {
        color: var(--danger);
    }
    form.invalid {
        border-color: var(--danger);
    }

}